.form {
  max-width: 394px;
  border: 1px solid #000;
  /* border: 1px solid #000; */
}

.form_type_profile {
  margin: 0 auto;
  padding: 123px 20px;
  margin: 20px 0 0 0;
  /* box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px; */
  /* background-color: #fff; */
}

.form__input-container {
  display: flex;
  flex-direction: column;
}

.form__input-container_way_grid {
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.form__label {
  font-size: 10px;
  line-height: 12px;
  color: #dccaca;
}

.form__input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #e8e8e8;
  background: #fff;
  font-size: 14px;
  line-height: 17px;
  min-height: 27px;
  margin: 0 0 24px 0;
}

.form__input:focus {
  border-bottom: 1px solid #ff6838;
}

.form__input-error {
  color: #ee3465;
  font-size: 10px;
  line-height: 12px;
  margin: -20px 0 6px 0;
}

.form__input-row {
  display: grid;
  grid-template-columns: 30px 1fr;
  grid-template-rows: 27px minmax(8px, 15px);
  column-gap: 20px;
  grid-template-areas:
  'label input'
  '. error';
}

.form__input_type_profile {
  text-align: right;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.form__label_type_profile {
  display: flex;
  align-items: center;
  width: 50px;
  font-weight: 500;
  font-size: 15px;
  line-height: 13px;
  box-sizing: border-box;
  /* margin: 0 0 24px 0; */
}

.form__label_type_profile-name {
  height: 27px;
  grid-area: label;
  border-bottom: 1px solid #e8e8e8;
}

.form__label_type_profile-email {
  height: 27px;
  grid-area: label;
  border: none;
}

.form__input_type_profile-name {
  height: 27px;
  grid-area: input;
  border-bottom: 1px solid #e8e8e8;
  border-radius: 3px;
  padding: 0 5px 0 0;
}

.form__input_type_profile-email {
  /* background-color: #c0bdbd; */
  padding: 0 5px 0 0;
  height: 27px;
  grid-area: input;
  border: none;
  border-radius: 3px;
}

.form__input-error_type_profile-name {
  grid-area: error;
  margin: 0;
}
.form__input-error_type_profile-email {
  grid-area: error;
  margin: 0;
}

.form__input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset;
}

.form__input:focus {
  outline: none;
}

.form__success {
  position: absolute;
  width: 100%;
  margin: -30px 0 0 0;
  text-align: center;
  font-size: 13px;
}

.form__success_type_profile {
  color: #c2c6e7;
  cursor: none;
}

.form__success_type_error {
  color: red;
}

.form__info {
  position: absolute;
  width: 100%;
  margin: -25px 0 0 0;
  text-align: center;
  font-size: 13px;
  color: #465dff;
}

.form__nothing-found {
  color: #465dff;
  display: flex;
  justify-content: center;
}

.form__save {
  width: 100%;
  margin: 135px 0 0 0;
  background: #ff6838;
  border-radius: 3px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  border: none;
  color: #fff;
  padding: 13px 0;
  cursor: pointer;
}

.form__save:focus {
  outline: none;
  background-image: linear-gradient(to right, #ff6838, #fb8a19, #ff6838, #e67621);
  color: #fff;
}

.form__save_type_profile {
  position: relative;
  border: 1px solid #000;
  margin: 220px 0 0 0;
  color: #ebe2e7;
  /* background: #fff; */
  text-decoration: underline;
  background: none;
  text-align: center;
  border: none;
  padding: 0;
  cursor: pointer;
  display: inline-block;
  padding: 2px 0;
}

.form__save_type_profile:focus {
  outline: 1px solid #ff6838;
}

.form__button_disable {
  background: #c0bdbd;
  opacity: .4;
  pointer-events: none;
  cursor: auto;
}

.form__button_type-profile_disable {
  cursor: auto;
  color: #d3d1d1;
  pointer-events: none;
  text-decoration: none;

  opacity: .5;
}

.form__edit {
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

.form__movies:focus {
  outline: none;
}

.form__text {
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a0a0a0;
  padding: 16px 0 70px 0;
}

.form__link {
  color: #ff6838;
  padding: 0 4px 0 0;
}

.form__link:focus {
  outline: 1px solid #ff6838;
}

.form__profile-exit {
  display: block;
  text-align: center;
  margin: 16px 0 40px 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  color: #ed7c9a;
}

.form__profile-exit:focus {
  outline: 1px solid #ff6838;
}

@media (max-width:550px){
  .form {
    padding-top: 80px;
    max-width: 260px;
    margin: 0 auto;
  }

  .form__text {
    font-size: 12px;
    line-height: 15px;
  }

  .form__link {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
  }

  .form__save {
    margin-top: 157px;
  }
}